@import "../../assets/styles/fonts";

.chose-tour {

  &_info {
    display: flex;
    flex-direction: column;
    row-gap: 106px;

    &_head {

      &_title {
        @include h2;

        color: var(--black);

        text-align: center;
      }
    }
  }
}

@media (max-width: 550px) {
  .chose-tour {
    &_info {
      row-gap: 50px;
    }
  }
}